import { useState, useContext } from "react";
import { MyCharsCt, MyWpsCt, MyArtsCt, MyStsCt } from "../../App";
import { ModalCarpet } from "../../styledCpns/Layout";
import { Button } from "../../styledCpns/Inputs";
import { Text } from "../../styledCpns/DataDisplay";
import { LoadBtn, StyledInner, CloseBtn, Divider } from "./styles";
import { useCloseModal } from "../../helpers/hooks";

export default function Download() {
  const [optionsOn, setOptionsOn] = useState(false);
  const [msType, setMsType] = useState(null);
  const data = JSON.stringify({
    Characters: useContext(MyCharsCt),
    Weapons: useContext(MyWpsCt),
    Artifacts: useContext(MyArtsCt),
    Setups: useContext(MyStsCt)
  });

  const saveToLocalStorage = () => {
    localStorage.setItem("GDC_Data", data);
    setOptionsOn(false);
    setMsType("success");
  };

  const trySaveToLocalStorage = () => {
    if (localStorage.getItem("GDC_Data")) {
      setOptionsOn(false);
      setMsType("warning");
    } else {
      saveToLocalStorage();
    }
  };

  const download = () => {
    const textBlob = new Blob([data], { type: "text/plain" });
    let newLink = document.createElement("a");
    newLink.download = "GDC_Data";
    if (window.webkitURL != null) {
      newLink.href = window.webkitURL.createObjectURL(textBlob);
    } else {
      newLink.href = window.URL.createObjectURL(textBlob);
      newLink.style.display = "none";
      document.body.appendChild(newLink);
    }
    newLink.click();
    setOptionsOn(false);
  };

  return (
    <div>
      <LoadBtn className="pointer" onClick={() => setOptionsOn(true)}>
        <i className="fas fa-download mr-2" />
        <Text fontWeight={700}>Download</Text>
      </LoadBtn>
      {optionsOn && (
        <Options
          trySaveToLocalStorage={trySaveToLocalStorage}
          download={download}
          close={() => setOptionsOn(false)}
        />
      )}
      {Boolean(msType) && (
        <Message
          isSuccess={msType === "success"}
          close={() => setMsType(null)}
          save={saveToLocalStorage}
        />
      )}
    </div>
  );
}

function Options(props) {
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <StyledInner ref={ref} className="pt-2 pb-3">
        <CloseBtn className="ml-auto mr-2 mb-2" onClick={props.close} />
        <div className="option" onClick={props.trySaveToLocalStorage}>
          <Text variant="h6" fontWeight={700}>
            Save to Local Storage
          </Text>
          <Text>
            • Your data stays in this Browser only and will be removed if you
            clear the Local Storage.
          </Text>
        </div>
        <Divider />
        <div className="option" onClick={props.download}>
          <Text variant="h6" fontWeight={700}>
            Download a .TXT file
          </Text>
          <Text>
            • Please DO NOT modify this file if you don't understand how it
            works.
          </Text>
        </div>
      </StyledInner>
    </ModalCarpet>
  );
}

function Message(props) {
  const { isSuccess } = props;
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <StyledInner ref={ref} className="px-3 py-4 flex-col">
        <Text variant="h5" fontWeight={700} align="center">
          {isSuccess
            ? "Successfully save to Local Storage!"
            : "We detect old data in your Local Storage. Do you want to replace it?"}
        </Text>
        <div className="mt-3 flex justify-center">
          {!isSuccess && (
            <Button className="mr-5 glow-on-hover" variant="negative">
              Cancel
            </Button>
          )}
          <Button
            className="glow-on-hover"
            variant="positive"
            onClick={() => {
              isSuccess ? props.close() : props.save();
            }}
          >
            {isSuccess ? "OK" : "Proceed"}
          </Button>
        </div>
      </StyledInner>
    </ModalCarpet>
  );
}
