import { useContext, useCallback } from "react";
import { MyCharsCt, MyWpsCt, MyArtsCt } from "../../../App";
import { FixedCts } from "../index";
import Picker from "../../Picker";
import { initWeapon } from "../../Picker/WpPicker";
import { getArts } from "../../MyCharacters/Info";
import {
  initSelfMCs,
  getMainWpBCs,
  getMainArtBCs,
  getSubArtBCs,
  getSubArtDCs,
  initParty,
  initElmtMCs,
  initTarget
} from "./helpers";
import characters from "../../../data/characters";

export default function CharPicker(props) {
  const myChars = useContext(MyCharsCt);
  const myWeapons = useContext(MyWpsCt);
  const myArtifacts = useContext(MyArtsCt);
  const {
    setSelfMCs,
    setWpRack,
    setSubWpBCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setTarget,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);
  const combinedList = getCombinedList(myChars);

  const pickChar = useCallback(
    (unit) => {
      const { name, weaponID, artIDs } = unit;
      props.setChar({
        name,
        level: unit.level || "1/20",
        ascension: unit.ascension || 0,
        "Normal Attack": unit["Normal Attack"] || 1,
        "Elemental Skill": unit["Elemental Skill"] || 1,
        "Elemental Burst": unit["Elemental Burst"] || 1,
        constellation: unit.constellation || 0
      });
      setSelfMCs([initSelfMCs(name)]);
      setWpRack(() => {
        if (weaponID) {
          const existedWp = myWeapons.find((wp) => wp.ID === weaponID);
          if (existedWp) {
            return [
              {
                ...existedWp,
                BCs: getMainWpBCs(existedWp.type, existedWp.name)
              }
            ];
          }
        }
        const newWp = initWeapon({ type: characters[name].weapon });
        return [{ ...newWp, BCs: getMainWpBCs(newWp.type, newWp.name) }];
      });
      setSubWpBCs([{}]);
      setArtChest(() => {
        const { pieces, sets } = getArts(artIDs, myArtifacts);
        const bnLv = sets[0]?.bonusLv === 1;
        return [
          {
            pieces,
            sets,
            BCs: getMainArtBCs(bnLv ? sets[0].name : null),
            subBCs: getSubArtBCs(bnLv ? sets[0].name : null),
            subDCs: getSubArtDCs()
          }
        ];
      });
      setParties([initParty()]);
      setElmtMCs([initElmtMCs()]);
      setTarget(initTarget());
      setSetups([{ name: "Setup 1", ID: Date.now() }]);
      setCurSetupI(0);
      setStdSetupI(0);
      setCmpSetupIs([0]);
    },
    [myWeapons, myArtifacts]
  );

  return (
    <Picker
      data={combinedList}
      dataType="Character"
      close={props.close}
      pick={pickChar}
    />
  );
}

function getCombinedList(myChars) {
  const result = [];
  for (let name in characters) {
    const { icon, rarity, vision, weapon } = characters[name];
    const char = { icon, rarity, vision, weapon };
    const existedUnit = myChars.find((unit) => unit.name === name);
    if (existedUnit) {
      result.push({ ...existedUnit, ...char });
    } else {
      result.push({ name, ...char });
    }
  }
  return result;
}
