import { useState, useEffect, useContext, createContext } from "react";
import { MyStsCt } from "../../App";
import { StyledCalculator } from "./styles";
import CalcChar from "./CalcChar";
import Modifiers from "./Modifiers";
import SetupManager from "./SetupManager";
import DmgResults from "./DmgResults";
import Message from "./Message";
import characters from "../../data/characters";
import monsters from "../../data/monsters";
import { elmts } from "../../data/elements";
import { initTarget } from "./CalcChar/helpers";
import { getNewSetupName } from "./SetupManager/Settings/helpers";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";
import { deepCopy } from "../../helpers/misc";
import { getFinalInfusion } from "../../helpers/processors";

export const FixedCts = createContext();
export const SelfMCsCt = createContext();
export const WpRackCt = createContext();
export const SubWpBCsCt = createContext();
export const ArtChestCt = createContext();
export const PartiesCt = createContext();
export const ElmtMCsCt = createContext();
export const TargetCt = createContext();
export const ResultCt = createContext();
export const SetupCt = createContext();
export const ManagerCts = createContext();

export default function Calculators(props) {
  const { char, setChar, editedID, toCalc } = props;
  const [selfMCs, setSelfMCs] = useState([]);
  const [wpRack, setWpRack] = useState([]);
  const [subWpBCs, setSubWpBCs] = useState([]);
  const [artChest, setArtChest] = useState([]);
  const [parties, setParties] = useState([]);
  const [elmtMCs, setElmtMCs] = useState([]);
  const [target, setTarget] = useState(initTarget());
  const [setups, setSetups] = useState([]);
  const [curSetupI, setCurSetupI] = useState(0);
  const [stdSetupI, setStdSetupI] = useState(0);
  const [cmpSetupIs, setCmpSetupIs] = useState([0]);

  const [monster, setMonster] = useState({
    name: monsters[0].name,
    inputs: []
  });
  useEffect(() => {
    setTarget((prev) => {
      const newTg = deepCopy(prev);
      const { res, addResBn } = monsters[
        monsters.findIndex(({ name }) => name === monster.name)
      ];
      for (let type in res) {
        if (type === "Elemental RES") {
          for (let elmt of elmts) {
            newTg[elmt + " RES"] = res[type];
          }
        } else {
          newTg[type] = res[type];
        }
      }
      if (addResBn) {
        addResBn({ target: newTg, inputs: monster.inputs });
      }
      return newTg;
    });
  }, [monster]);

  const mySetups = useContext(MyStsCt);
  const [ms, setMs] = useState(null);
  function addDifferences(st) {
    setSelfMCs((prev) => [...prev, st.selfMCs]);
    setWpRack((prev) => [...prev, st.weapon]);
    setSubWpBCs((prev) => [...prev, st.subWpBCs]);
    setArtChest((prev) => [...prev, st.arts]);
    setParties((prev) => [...prev, st.party]);
    setElmtMCs((prev) => [...prev, st.elmtMCs]);
    setSetups((prev) => [...prev, { name: getNewSetupName(prev), ID: st.ID }]);
    setCurSetupI(setups.length);
  }
  useEffect(() => {
    const editedSt = editedID
      ? mySetups.find((st) => st.ID === editedID)
      : null;
    if (editedSt) {
      if (char) {
        if (char.name === editedSt.char.name) {
          if (setups.length === 4) {
            setMs(2);
          } else {
            let sameTg = true;
            for (let type in target) {
              if (target[type] !== editedSt.target[type]) {
                sameTg = false;
                break;
              }
            }
            if (sameTg) {
              addDifferences(editedSt);
              toCalc();
            } else {
              setMs(3);
            }
          }
        } else {
          setMs(1);
        }
      } else {
        setChar(editedSt.char);
        addDifferences(editedSt);
        setTarget(editedSt.target);
        toCalc();
      }
    }
  }, [editedID]);

  let charData;
  const allATTRs = [];
  const allArtSBnes = [];
  const allRxnBnes = [];
  const allInfusion = [];
  const allDmg = [];
  if (char) {
    charData = characters[char.name];
    for (let i = 0; i < setups.length; i++) {
      const finalInfusion = getFinalInfusion(
        selfMCs[i],
        charData.vision,
        parties[i]
      );
      allInfusion.push(finalInfusion);
      const { ATTRs, artSBnes, rxnBnes, bonuses } = getBuffedStats(
        charData,
        char,
        selfMCs[i].BCs,
        wpRack[i],
        subWpBCs[i],
        artChest[i],
        elmtMCs[i].resonance,
        parties[i],
        finalInfusion
      );
      allATTRs.push(ATTRs);
      allArtSBnes.push(artSBnes);
      allRxnBnes.push(rxnBnes);
      allDmg.push(
        getDmg(
          deepCopy(char),
          selfMCs[i],
          parties[i],
          wpRack[i],
          artChest[i].subDCs,
          ATTRs,
          bonuses,
          rxnBnes,
          finalInfusion,
          elmtMCs[i],
          target
        )
      );
    }
  }
  return (
    <FixedCts.Provider
      value={{
        setSelfMCs,
        setWpRack,
        setSubWpBCs,
        setArtChest,
        setParties,
        setElmtMCs,
        setTarget,
        setMonster,
        setSetups,
        setStdSetupI,
        setCurSetupI,
        setCmpSetupIs
      }}
    >
      <SelfMCsCt.Provider value={selfMCs}>
        <WpRackCt.Provider value={wpRack}>
          <SubWpBCsCt.Provider value={subWpBCs}>
            <ArtChestCt.Provider value={artChest}>
              <PartiesCt.Provider value={parties}>
                <ElmtMCsCt.Provider value={elmtMCs}>
                  <TargetCt.Provider value={{ target, monster }}>
                    <ResultCt.Provider
                      value={{
                        allInfusion,
                        allATTRs,
                        allArtSBnes,
                        allRxnBnes,
                        allDmg
                      }}
                    >
                      <SetupCt.Provider value={curSetupI}>
                        <StyledCalculator>
                          <div className="flex">
                            <CalcChar
                              char={char}
                              charData={charData}
                              setChar={setChar}
                            />
                            <Modifiers char={char} />
                            <ManagerCts.Provider
                              value={{
                                setups,
                                stdSetupI,
                                cmpSetupIs
                              }}
                            >
                              <SetupManager char={char} charData={charData} />
                              <DmgResults char={char} />
                            </ManagerCts.Provider>
                            {ms ? (
                              <Message
                                type={ms}
                                editedSt={mySetups.find(
                                  (st) => st.ID === editedID
                                )}
                                setChar={setChar}
                                addDifferences={addDifferences}
                                toCalc={toCalc}
                                close={() => {
                                  setMs(null);
                                  props.setEditedID(null);
                                }}
                              />
                            ) : null}
                          </div>
                        </StyledCalculator>
                      </SetupCt.Provider>
                    </ResultCt.Provider>
                  </TargetCt.Provider>
                </ElmtMCsCt.Provider>
              </PartiesCt.Provider>
            </ArtChestCt.Provider>
          </SubWpBCsCt.Provider>
        </WpRackCt.Provider>
      </SelfMCsCt.Provider>
    </FixedCts.Provider>
  );
}
