import { useState } from "react";
import { Box, CardCarpet } from "../../../styledCpns/Layout";
import { Img } from "../../../styledCpns/DataDisplay";
import { SetBonus, OneBtnBar } from "../../CommonCpns";
import ArtAllStats from "./ArtAllStats";
import artifacts from "../../../data/artifacts";
import { wikia, artifactIcons } from "../../../data/misc";

export default function ArtOverview(props) {
  const [statsOn, setStatsOn] = useState(false);
  const { pieces, sets } = props.arts;
  return (
    <CardCarpet className="relative">
      <Box className="flex-wrap justify-center " width="18.75rem">
        {["flower", "sands", "circlet", "plume", "goblet"].map((type, i) => (
          <ArtPiece
            key={i}
            type={type}
            art={pieces[type]}
            setArtInvType={props.setArtInvType}
            artCardType={props.artCardType}
            setArtCardType={props.setArtCardType}
            showing={props.showing}
            setShowing={props.setShowing}
          />
        ))}
      </Box>
      <Box className="mt-3 hide-sb" width="18.75rem" height="14rem">
        <SetBonus sets={sets} />
      </Box>
      <OneBtnBar
        className="mt-3"
        variant="positive"
        text="Stats Bonus"
        handleClick={() => setStatsOn(true)}
      />
      {statsOn && <ArtAllStats close={() => setStatsOn(false)} />}
    </CardCarpet>
  );
}

function ArtPiece(props) {
  const { type, art, artCardType } = props;
  let content;
  if (art) {
    const { icon, rarity = "Gold" } = artifacts[art.set][type];
    content = (
      <Box
        className={
          "p-1 pointer flex " +
          (type === artCardType ? "glowing" : "cmn-shadow")
        }
        borderRadius="50%"
        bgImg={rarity}
        onClick={() => {
          if (type === artCardType) {
            props.setShowing(false);
            setTimeout(() => props.setArtCardType(null), 200);
          } else {
            props.setShowing(true);
            props.setArtCardType(type);
          }
        }}
      >
        <img src={wikia + icon + ".png"} alt="" />
      </Box>
    );
  } else {
    content = (
      <Img
        className="p-3 pointer glow-on-hover"
        width="100%"
        borderRadius="50%"
        bgColor="cardBg3"
        src={wikia + artifactIcons[type] + ".png"}
        alt=""
        onClick={() => props.setArtInvType(type)}
      />
    );
  }
  return (
    <Box className="px-2 flex" width="6.25rem" height="5.25rem">
      {content}
    </Box>
  );
}
