import { getCharDCs } from "../CalcChar/helpers";
import characters from "../../../data/characters";
import weapons from "../../../data/weapons";

function getTeammateBCs(tmName) {
  const result = [];
  const { buffs } = characters[tmName];
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "self") {
        const node = { activated: false, index };
        if (buff.labels) {
          node.inputs = [...buff.inputs];
        }
        result.push(node);
      }
    });
  }
  return result;
}

export function initTeammate(name) {
  return {
    name,
    BCs: getTeammateBCs(name),
    DCs: getCharDCs(name, false),
    infusion: null
  };
}

export function visionCount(char, party) {
  let count = {};
  count[characters[char.name].vision] = 1;
  for (let teammate of party) {
    if (teammate.name) {
      const { vision } = characters[teammate.name];
      count[vision] = (count[vision] || 0) + 1;
    }
  }
  return count;
}

export function getWpRange(party) {
  const result = {};
  for (let teammate of party) {
    if (teammate.name) {
      result[characters[teammate.name].weapon] = 1;
    }
  }
  return result;
}

export function getSubWpBCs(wpType) {
  const result = [];
  const weapon = weapons[wpType];
  for (let name in weapon) {
    const { buffs } = weapon[name];
    if (buffs) {
      buffs.forEach((buff, index) => {
        if (buff.affect !== "self") {
          const node = { name, activated: false, refinement: 1, index };
          if (buff.inputTypes) {
            node.inputs = [];
            buff.inputTypes.forEach((type, j) => {
              if (type === "choices") {
                node.inputs.push(buff.values[j][0]);
              }
            });
          }
          result.push(node);
        }
      });
    }
  }
  return result;
}
