import { useContext, createContext } from "react";
import { MyCharsCt, MyWpsCt, MyArtsCt } from "../../../App";
import { Box } from "../../../styledCpns/Layout";
import SectionAttr from "./SectionAttr";
import SectionWp from "./SectionWp";
import SectionArts from "./SectionArts";
import SectionCons from "./SectionCons";
import SectionTalent from "./SectionTalent";
import characters from "../../../data/characters";
import { getArtSets } from "../../../helpers/processors";
import getBaseStats from "../../../calculators/baseStats";

export const ArtStatsCt = createContext();

export default function Info({ charIndex }) {
  const myChars = useContext(MyCharsCt);
  const myWeapons = useContext(MyWpsCt);
  const myArtifacts = useContext(MyArtsCt);
  const { weaponID, artIDs, ...char } = myChars[charIndex];
  const charData = characters[char.name];
  const { weapon, weaponI } = getWpInfo(weaponID, myWeapons);
  const arts = getArts(artIDs, myArtifacts);
  const { ATTRs, artSBnes } = getBaseStats(charData, char, weapon, arts);

  return (
    <Box className="pb-5 flex overflow-auto" width="87%">
      <SectionAttr
        charData={charData}
        char={char}
        charI={charIndex}
        ATTRs={ATTRs}
      />
      {Boolean(weapon) && (
        <SectionWp
          owner={char.name}
          ownerIndex={charIndex}
          weapon={weapon}
          weaponI={weaponI}
        />
      )}
      <ArtStatsCt.Provider value={artSBnes}>
        <SectionArts
          owner={char.name}
          ownerIndex={charIndex}
          arts={arts}
          artIDs={artIDs}
          artSBnes={artSBnes}
        />
      </ArtStatsCt.Provider>
      <SectionCons char={char} charI={charIndex} />
      <SectionTalent char={char} charI={charIndex} />
    </Box>
  );
}

function getWpInfo(weaponID, myWeapons) {
  for (let i in myWeapons) {
    if (myWeapons[i].ID === weaponID) {
      return { weapon: myWeapons[i], weaponI: i };
    }
  }
  return {};
}

export function getArts(artIDs = {}, myArtifacts) {
  const pieces = {
    flower: null,
    plume: null,
    sands: null,
    goblet: null,
    circlet: null
  };
  for (let type in pieces) {
    if (artIDs[type]) {
      pieces[type] = myArtifacts.find((piece) => piece.ID === artIDs[type]);
    }
  }
  return { pieces, sets: getArtSets(pieces) };
}
