import styled from "styled-components";
import { useState } from "react";
import { Box, ModalCarpet } from "../../../styledCpns/Layout";
import { Img } from "../../../styledCpns/DataDisplay";
import WeaponCard from "../../Readers/WeaponCard";
import ArtifactCard from "../../Readers/ArtifactCard";
import { wikia, artifactIcons } from "../../../data/misc";
import weapons from "../../../data/weapons";
import artifacts from "../../../data/artifacts";
import { useCloseModal } from "../../../helpers/hooks";

const Case = (props) => (
  <div className="p-1">
    <Box
      className={"p-1" + (props.bgImg ? " pointer" : "")}
      bgImg={props.bgImg}
      borderRadius="0.25rem"
      onClick={props.popModal}
    >
      <Img width="4.25rem" src={wikia + props.icon + ".png"} alt="" />
    </Box>
  </div>
);

export default function Gears(props) {
  const { weapon, artPieces } = props;
  const wpData = weapons[weapon.type][weapon.name];
  const [type, setType] = useState(null);
  return (
    <Box className="flex-wrap" width="15.75rem">
      <Case
        bgImg={wpData.rarity}
        icon={wpData.icon}
        popModal={() => setType("weapon")}
      />
      {Object.entries(artPieces).map((pair, i) => {
        if (pair[1]) {
          const { icon, rarity = "Gold" } = artifacts[pair[1].set][pair[0]];
          return (
            <Case
              key={i}
              bgImg={rarity}
              icon={icon}
              popModal={() => setType("artifacts")}
            />
          );
        }
        return <Case key={i} icon={artifactIcons[pair[0]]} />;
      })}
      {type ? (
        <GearReader
          type={type}
          weapon={weapon}
          artPieces={artPieces}
          close={() => setType(null)}
        />
      ) : null}
    </Box>
  );
}

const StyledDiv = styled(Box)`
  max-width: 90%;
`;

function GearReader(props) {
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <StyledDiv
        ref={ref}
        className="px-3 py-4 flex custom-sb white-glow-shadow"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        {props.type === "weapon" ? (
          <Box className="hide-sb" width="18.75rem" height="30rem">
            <WeaponCard mutable={false} weapon={props.weapon} />
          </Box>
        ) : (
          Object.values(props.artPieces).map((art, i) =>
            art ? (
              <Box key={i} className="px-1" width="15.5rem">
                <ArtifactCard mutable={false} art={art} />
              </Box>
            ) : null
          )
        )}
      </StyledDiv>
    </ModalCarpet>
  );
}
