import { useContext } from "react";
import { WpRackCt, SubWpBCsCt, SetupCt } from "../../index";
import { NoContent } from "../CmnCpns";
import WpBuffsMain from "./WpBuffsMain";
import WpBuffsSub from "./WpBuffsSub";

export default function WpBuffs() {
  const wpRack = useContext(WpRackCt);
  const subWpBCs = useContext(SubWpBCsCt);
  const curSetupI = useContext(SetupCt);
  const weapon = wpRack[curSetupI];
  if (!weapon.BCs.length) {
    let noContent = true;
    for (let wpType in subWpBCs[curSetupI]) {
      const bc = subWpBCs[curSetupI][wpType];
      if (bc && bc.length) {
        noContent = false;
        break;
      }
    }
    if (noContent) return <NoContent type="buffs" />;
  }
  return (
    <>
      <WpBuffsMain weapon={weapon} />
      <WpBuffsSub subBCs={subWpBCs[curSetupI]} />
    </>
  );
}
