import { useContext } from "react";
import { FixedCts } from "./index";
import { Confirmation } from "../CommonCpns";
import { initTarget } from "./CalcChar/helpers";

export default function Message(props) {
  const { type, editedSt, addDifferences, toCalc, close } = props;
  const {
    setSelfMCs,
    setWpRack,
    setSubWpBCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setTarget,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);

  function initNewSession() {
    props.setChar(editedSt.char);
    setSelfMCs([editedSt.selfMCs]);
    setWpRack([editedSt.weapon]);
    setSubWpBCs([editedSt.subWpBCs]);
    setArtChest([editedSt.arts]);
    setParties([editedSt.party]);
    setElmtMCs([editedSt.elmtMCs]);
    setSetups([{ name: editedSt.name, ID: editedSt.ID }]);
    setTarget(initTarget());
    setStdSetupI(0);
    setCurSetupI(0);
    setCmpSetupIs([0]);
    toCalc();
  }

  if (type === 1 || type === 2) {
    return (
      <Confirmation
        message={
          (type === 1
            ? "We're calculating another Character."
            : "The number of Setups on Calculator has reach the limit of 4.") +
          " Do you wish to start a new session?"
        }
        confirm={initNewSession}
        close={close}
      />
    );
  }
  return (
    <Confirmation
      message="There's a different Target on the Calculator. Do you wish to override?"
      otherBtn={{
        text: "Keep it",
        handleClick: () => {
          addDifferences(editedSt);
          toCalc();
        }
      }}
      confirm={() => {
        addDifferences(editedSt);
        setTarget(editedSt.target);
        toCalc();
      }}
      close={close}
    />
  );
}
