import { useContext } from "react";
import { MyWpsCt, SetDBCt } from "../../App";
import { deepCopy } from "../../helpers/misc";
import { CardCarpet } from "../../styledCpns/Layout";
import { OneBtnBar, EquippedChar } from "../CommonCpns";
import MyWpCard from "../Readers/MyWpCard";
import { initWeapon } from "../Picker/WpPicker";

export default function RightSection(props) {
  const { chosenI } = props;
  const myWeapons = useContext(MyWpsCt);
  const weapon = myWeapons[chosenI];
  const { setMyChars, setMyWps } = useContext(SetDBCt);
  return (
    <div className="flex-col justify-between">
      <CardCarpet>
        <div className="card_content hide-sb">
          {weapon ? <MyWpCard weapon={weapon} mutable={true} /> : null}
        </div>
        {weapon ? (
          <OneBtnBar
            className="mt-3"
            variant="negative"
            text="Remove"
            handleClick={() => {
              const { user } = weapon;
              const newWpID = Date.now();
              setMyWps((prev) => {
                const newWps = deepCopy(prev);
                newWps.splice(chosenI, 1);
                if (user) {
                  newWps.push(
                    initWeapon({ type: weapon.type, ID: newWpID, user })
                  );
                }
                return newWps;
              });
              if (user) {
                setMyChars((prev) => {
                  const newChars = deepCopy(prev);
                  newChars[
                    newChars.findIndex((char) => char.name === user)
                  ].weaponID = newWpID;
                  return newChars;
                });
              }
            }}
          />
        ) : null}
      </CardCarpet>
      {weapon?.user ? <EquippedChar user={weapon.user} /> : null}
    </div>
  );
}
