import styled from "styled-components";
import { useContext, useState, useEffect, useCallback } from "react";
import { ManagerCts, FixedCts } from "../../index";
import { CloseBtn, IconBtn } from "../../../../styledCpns/Inputs";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Box } from "../../../../styledCpns/Layout";
import { OneBtnBar } from "../../../CommonCpns";
import SetupCtrl from "./SetupCtrl";
import { SaveCtrl, SharedCtrl } from "./ExtraCtrls";
import { colorCodes } from "../../../../data/misc";
import { applySettings, getNewSetupName } from "./helpers";

const StyledDiv = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0.625rem;
  background-color: ${colorCodes.cardBg3};
`;

export default function Settings(props) {
  const { char, wpType, close } = props;
  const { setups, stdSetupI, cmpSetupIs } = useContext(ManagerCts);
  const {
    setSelfMCs,
    setWpRack,
    setSubWpBCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);
  const [tempSetups, setTempSetups] = useState([]);
  const [err, setErr] = useState(false);
  const [savedI, setSavedI] = useState(null);
  const [sharedI, setSharedI] = useState(null);

  useEffect(() => {
    setTempSetups(
      setups.map((st, index) => ({
        ...st,
        index,
        isStandard: index === stdSetupI,
        checked: cmpSetupIs.includes(index),
        expanded: false
      }))
    );
  }, [setups]);

  const charName = char.name;
  const tryApply = useCallback(() => {
    let ok = true;
    let standardI;
    const names = [];
    for (let i in tempSetups) {
      const { name, isStandard } = tempSetups[i];
      if (!name.length || names.includes(name)) {
        ok = false;
        break;
      } else {
        names.push(name);
      }
      if (isStandard) standardI = +i;
    }
    if (ok) {
      applySettings(tempSetups, charName, wpType, {
        setSetups,
        setCmpSetupIs,
        setSelfMCs,
        setWpRack,
        setSubWpBCs,
        setArtChest,
        setParties,
        setElmtMCs
      });
      setStdSetupI(standardI);
      setCurSetupI(standardI);
      close();
    } else {
      setErr(true);
    }
  }, [tempSetups, charName, wpType]);

  return (
    <StyledDiv className="full-w full-h p-3 flex-col">
      <CloseBtn variant="negative" className="glow-on-hover" onClick={close} />
      <Text className="mt-2" variant="h3" align="center" color="orange">
        SETTINGS
      </Text>
      <Box height="33.45rem" className="flex-col hide-sb">
        {tempSetups.map((setup, index) => (
          <SetupCtrl
            key={index}
            setup={setup}
            index={index}
            belowLimit={tempSetups.length < 4}
            setTempSetups={setTempSetups}
            save={() => setSavedI(index)}
            share={() => setSharedI(index)}
          />
        ))}
        {tempSetups.length < 4 && (
          <IconBtn
            className="mt-3 py-2 full-w glow-on-hover"
            icon="fas fa-plus"
            bgColor="#2269d6"
            color="white"
            borderRadius="1rem"
            onClick={() => {
              setTempSetups((prev) => {
                const newTSs = [...prev];
                newTSs.push({
                  name: getNewSetupName(prev),
                  ID: Date.now(),
                  index: null,
                  isStandard: false,
                  checked: false,
                  expanded: false
                });
                return newTSs;
              });
            }}
          />
        )}
        {err && (
          <Text className="mt-3" color="lightRed" align="center">
            <i>Please choose a unique name for each Setup.</i>
          </Text>
        )}
      </Box>
      <OneBtnBar
        className="mt-3"
        variant="positive"
        text="Apply"
        handleClick={tryApply}
      />
      {savedI !== null && (
        <SaveCtrl
          char={char}
          tempSetups={tempSetups}
          savedI={savedI}
          close={() => setSavedI(null)}
        />
      )}
      {sharedI !== null && (
        <SharedCtrl
          char={char}
          tempSetups={tempSetups}
          sharedI={sharedI}
          close={() => setSharedI(null)}
        />
      )}
    </StyledDiv>
  );
}
